import { AuthService } from 'src/app/services/auth.service';
import { of } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { IonRefresher } from '@ionic/angular';
import { UserService } from 'src/app/services/user.service';
import { Discount } from './../../models/discount';
import { AlertService } from './../../services/alert.service';
import { Component, OnInit } from '@angular/core';
import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';

@UntilDestroy()
@Component({
  selector: 'app-affiliate-code',
  templateUrl: './affiliate-code.page.html',
  styleUrls: ['./affiliate-code.page.scss'],
})
export class AffiliateCodePage implements OnInit {
  affilitateCode = null;
  discounts:Discount[] = [];
  refresher:IonRefresher = null;
  isSubmitting = false;
  constructor(
    private alert:AlertService,
    private userService:UserService,
    private auth:AuthService
  ) { }

  ngOnInit() {
    this.userService.discountsShared.getUpdateNotifier(this).subscribe(result=>{
      this.discounts = this.userService.discountsShared.resource();
      if(this.refresher)
      {
        this.refresher.complete();
      }
    });
    this.auth.getUser().then((user) => {
      this.affilitateCode = user.phone;
    })

    this.userService.discountsShared.request();
  }
  refresh(ev)
  {
    this.refresher = ev.target;
    this.userService.discountsShared.request();
  }
  useDiscount(discountId)
  {
    this.isSubmitting = true;
    this.userService.useDiscount(discountId).pipe(catchError(error=>{
      this.alert.presentError("شارژ حساب با مشکل روبرو شد.");
      return of(-1);
    })).subscribe(result=>{
      if(result != 1)
      {
        this.alert.presentSuccess("حساب شما شارژ شد.");
        this.userService.balanceShared.request();
        this.userService.discountsShared.request();

      }
      this.isSubmitting = false;
    })
  }
  copyToClipboard()
  {
    navigator.clipboard.writeText(this.affilitateCode).then(()=>{
      this.alert.presentToast("کد معرفی کپی شد.");
    })
  }
  ngOnDestroy(): void {
    //Called once, before the instance is destroyed.
    //Add 'implements OnDestroy' to the class.
    
  }

}
